@import './../../styles/sass/variables';

.social {
    width: 50px;
    height: 50px;
    background-color: $textColor;
    background-size: cover;
    background-position: center;
    cursor: pointer;
    border-radius: 3px;
    &:hover {
        background-color: $titleColor;
        transition: 7ms ease-in-out;
    }
}

